import React, { useState } from "react";
// Sound effects
import incomeMoney from "../assets/incomeMoney.mp3";
import Moment from "moment";
export default function AddTransactions({ id, addTransaction }) {
  const [showAddTransaction, setShowAddTransaction] = useState(false);
  const [transactionAmount, setTransactionAmount] = useState(0);
  const [transactionDate, setTransactionDate] = useState("");
  const [transactionFrom, setTransactionFrom] = useState("");
  const [transactionTo, setTransactionTo] = useState("");

  const onSubmit = (event) => {
    event.preventDefault();

    const newTransaction = {
      id: id,
      amount: transactionAmount,
      date: transactionDate,
      from: transactionFrom,
      to: transactionTo,
    };

    addTransaction(newTransaction);

    setTransactionAmount(0);
    setTransactionDate("");
    setTransactionTo("");
    setTransactionFrom("");
    // Play sound based on transaction amount
    if (transactionAmount > 0) {
      playSound(incomeMoney);
    }
    setShowAddTransaction(false);
  };

  const playSound = (soundFile) => {
    const audio = new Audio(soundFile);
    audio.play();
  };

  const handleAmountChange = (event) => {
    const value = event.target.value;
    // Regex to allow only numbers and optional minus sign
    const isValidInput = /^-?\d*\.?\d*$/.test(value);
    if (isValidInput) {
      setTransactionAmount(value);
    }
  };

  const handleDateChange = (event) => {
    setTransactionDate(event.target.value);
  };

  const handleFromChange = (event) => {
    setTransactionFrom(event.target.value);
  };

  const handleToChange = (event) => {
    setTransactionTo(event.target.value);
  };
  const getFormattedDate = () => {
    const today = new Date();
    return Moment(today).format("DD-MM-YYYY");
  };
  return (
    <div>
      <button
        className="Add-btn"
        onClick={() => {
          setShowAddTransaction(!showAddTransaction);
        }}
      >
        {showAddTransaction ? "CANCEL" : "ADD"}
      </button>

      {showAddTransaction && (
        <div className="modal AddTransactionContainer">
          <div className="modal-content">
            <h3>Add work hours</h3>
            <p>Today's date is {getFormattedDate()}</p>
            <form onSubmit={onSubmit}>
              <div className="form-control">
                <label>Date:</label>
                <input
                  type="date"
                  value={transactionDate}
                  onChange={handleDateChange}
                  required
                />
              </div>
              <div className="form-control">
                <label>Start:</label>
                <input
                  value={transactionFrom}
                  onChange={handleFromChange}
                  placeholder="Source of the transaction"
                  type="time"
                  min="12:00"
                  max="24:00"
                  required
                />
              </div>
              <div className="form-control">
                <label>Finish:</label>
                <input
                  value={transactionTo}
                  onChange={handleToChange}
                  placeholder="Destination of the transaction"
                  type="time"
                  min="12:00"
                  max="24:00"
                  required
                />
              </div>
              <div className="form-control">
                <label>Wage:</label>
                <input
                  value={transactionAmount}
                  onChange={handleAmountChange}
                  placeholder="0"
                  type="number"
                  required
                />
              </div>
              <button className="Add-btn" type="submit">
                Add Transaction
              </button>
              <button
                className="cancel-btn"
                onClick={() => setShowAddTransaction(false)}
              >
                Cancel
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
