import React, { useState } from "react";

import Moment from "moment";
import html2canvas from "html2canvas";


export default function TransactionsList({ transactions, deleteTransaction }) {
  const [showConfirmation, setShowConfirmation] = useState(false);
  const [transactionIdToDelete, setTransactionIdToDelete] = useState(null);
  const [showWeekDays, setShowWeekDays] = useState(false);
  const [selectedWeek, setSelectedWeek] = useState(null);

  transactions = transactions.sort((a, b) => {
    return new Date(b.date) - new Date(a.date);
  });

  // Toggle transactions organization
  const toggleList = (weekNumber) => {
    setSelectedWeek(weekNumber);
    setShowWeekDays(true); // Show week days when toggling list
  };

  // Delete Button transaction
  const handleDelete = (id) => {
    setTransactionIdToDelete(id);
    setShowConfirmation(true);
  };

  // Call confirmation to delete transaction
  const handleConfirmation = (confirmed) => {
    if (confirmed) {
      deleteTransaction(transactionIdToDelete);
    }
    setShowConfirmation(false);
    setTransactionIdToDelete(null);
  };

  const getFormattedDate = () => {
    const today = new Date();
    return Moment(today).format("DD-MM-YYYY");
  };

  const takeScreenShot = (weekNumber) => {
    const element = document.getElementById("table");
    if (!element) {
      console.error("Table element not found");
      return;
    }
    html2canvas(element)
      .then((canvas) => {
        let image = canvas.toDataURL("image/jpeg");
        

        const a = document.createElement("a");
        a.href = image;
        a.download = `Week ${weekNumber}.jpeg`; // Concatenate weekNumber
        a.click();
      })
      .catch((err) => {
        console.error("Error capturing screenshot", err);
      });
  };

  return (
    <div>
      {transactions.length === 0 ? (
        <div>
         
        </div>
      ) : (
        <h2>My weeks</h2>
      )}

      {Object.entries(groupTransactionsByWeek(transactions)).map(
        ([weekNumber, weekTransactions]) => (
          <div key={weekNumber}>
            <h3 className="weeknumber" onClick={() => toggleList(weekNumber)}>
              Week {weekNumber}
            </h3>

            {showWeekDays && selectedWeek === weekNumber && (
              <div className="modal showweekdays">
                <div className="modal-content">
                  <h3>Today's date is {getFormattedDate()}</h3>
                  <button
                    className="save-btn"
                    onClick={() => takeScreenShot(weekNumber)}
                  >
                    Take ScreenShot
                  </button>
                  <table className="transactions-table" id="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Date</th>
                        <th>Start</th>
                        <th>Finish</th>
                        <th>Hours</th>
                        <th>Earnings</th>
                      </tr>
                    </thead>
                    <tbody>
                      {weekTransactions.map((transaction, index) => {
                        const fromTime = new Date(
                          `2022-01-01T${transaction.from}`
                        );
                        const toTime = new Date(`2022-01-01T${transaction.to}`);
                        const hoursWorked =
                          (toTime - fromTime) / (1000 * 60 * 60);
                        const earnings = transaction.amount * hoursWorked;
                        const hours = Math.floor(hoursWorked);
                        const minutes = Math.round((hoursWorked - hours) * 60);
                        return (
                          <tr key={index} className="transaction-row">
                            <td>{index + 1}</td>
                            <td>
                              {Moment(transaction.date).format("DD/M/YY")}
                            </td>
                            <td>{transaction.from}</td>
                            <td>{transaction.to}</td>
                            <td>
                              {hours} h {minutes} min
                            </td>
                            <td className="positiveAmount">
                              £ {earnings.toFixed(2)}
                            </td>
                            <td className="delete-button-container">
                              <div className="delete-button-wrapper">
                                <button
                                  className="delete-btn"
                                  onClick={() => handleDelete(transaction.id)}
                                >
                                  Delete
                                </button>
                              </div>
                            </td>
                          </tr>
                        );
                      })}
                      <tr>
                        <td colSpan="2" style={{ textAlign: "right" }}>
                          Total Hours:
                        </td>
                        <td>{getWeekTotalHours(weekTransactions)}</td>
                        <td colSpan="2" style={{ textAlign: "right" }}>
                          Total Earnings:
                        </td>
                        <td className="positiveAmount">
                          £ {getWeekTotalEarnings(weekTransactions).toFixed(2)}
                        </td>
                      </tr>
                    </tbody>
                  </table>

                  <button
                    className="cancel-btn"
                    onClick={() => setShowWeekDays(false)}
                  >
                    Close
                  </button>
                </div>
              </div>
            )}
          </div>
        )
      )}

      {showConfirmation && (
        <div className="confirmation-overlay">
          <div className="confirmation-dialog">
            <p>Are you sure you want to delete this day?</p>
            <div>
              <button
                className="save-btn"
                onClick={() => handleConfirmation(true)}
              >
                Yes, Sure
              </button>
              <button
                className="cancel-btn"
                onClick={() => handleConfirmation(false)}
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

function groupTransactionsByWeek(transactions) {
  const groupedTransactions = {};
  transactions.forEach((transaction) => {
    const date = new Date(transaction.date);
    const weekNumber = Moment(date).isoWeek();
    if (!groupedTransactions[weekNumber]) {
      groupedTransactions[weekNumber] = [];
    }
    groupedTransactions[weekNumber].push(transaction);
  });
  return groupedTransactions;
}

function getWeekTotalHours(transactions) {
  let totalHours = 0;
  let totalMinutes = 0;

  transactions.forEach((transaction) => {
    const fromTime = new Date(`2022-01-01T${transaction.from}`);
    const toTime = new Date(`2022-01-01T${transaction.to}`);
    const hoursWorked = (toTime - fromTime) / (1000 * 60 * 60);

    totalHours += Math.floor(hoursWorked);
    totalMinutes += Math.round((hoursWorked - Math.floor(hoursWorked)) * 60);
  });

  // If total minutes exceed 60, add to total hours
  const extraHours = Math.floor(totalMinutes / 60);
  totalMinutes %= 60; // Update total minutes with remainder

  return `${totalHours + extraHours} h ${totalMinutes} min`;
}

function getWeekTotalEarnings(transactions) {
  return transactions.reduce((total, transaction) => {
    const fromTime = new Date(`2022-01-01T${transaction.from}`);
    const toTime = new Date(`2022-01-01T${transaction.to}`);
    const hoursWorked = (toTime - fromTime) / (1000 * 60 * 60);
    const earnings = transaction.amount * hoursWorked;
    return total + earnings;
  }, 0);
}
