import React from "react";

export default function IncomeExpense({ transactions }) {
  // Get the current month
  const currentMonth = new Date().getMonth() + 1; // Months are zero-indexed, so add 1

  // Get the current month name
  const currentMonthName = new Date().toLocaleString("default", {
    month: "long",
  });

  // Filter transactions that belong to the current month
  const currentMonthTransactions = transactions.filter(
    (transaction) => new Date(transaction.date).getMonth() + 1 === currentMonth
  );

  // Extract unique dates from transactions
  const uniqueDates = Array.from(
    new Set(currentMonthTransactions.map((transaction) => transaction.date))
  );

  // Count the number of unique dates
  const totalDaysWorked = uniqueDates.length;

  // Calculate total earnings for the current month
  const totalEarnings = currentMonthTransactions.reduce(
    (total, transaction) => {
      // Assuming transaction.amount is the amount earned per hour
      const fromTime = new Date(`2022-01-01T${transaction.from}`);
      const toTime = new Date(`2022-01-01T${transaction.to}`);
      const hoursWorked = (toTime - fromTime) / (1000 * 60 * 60);
      return total + transaction.amount * hoursWorked;
    },
    0
  );

  // Calculate total hours and minutes
  const totalHours = currentMonthTransactions.reduce((total, transaction) => {
    const fromTime = new Date(`2022-01-01T${transaction.from}`);
    const toTime = new Date(`2022-01-01T${transaction.to}`);
    const hoursWorked = (toTime - fromTime) / (1000 * 60 * 60);
    return total + Math.floor(hoursWorked);
  }, 0);

  let totalMinutes = currentMonthTransactions.reduce((total, transaction) => {
    const fromTime = new Date(`2022-01-01T${transaction.from}`);
    const toTime = new Date(`2022-01-01T${transaction.to}`);
    const hoursWorked = (toTime - fromTime) / (1000 * 60 * 60);
    return total + Math.round((hoursWorked - Math.floor(hoursWorked)) * 60);
  }, 0);

  // If total minutes exceed 60, add to total hours
  const extraHours = Math.floor(totalMinutes / 60);
  totalMinutes %= 60; // Update total minutes with remainder

  const totalHoursFormatted = `${totalHours + extraHours}h ${totalMinutes}mins`;

  return (
    <div>
      <div className="Container">
        <header>My Hours</header>

        <div className="BalanceBoxquestion">
          How much have I earned this month?
        </div>
        {totalEarnings === 0 ? (
          <div className="BalanceBox" style={{ color: "#333" }}>
            £ {totalEarnings.toFixed(2)}
          </div>
        ) : (
          <div className="BalanceBox">+ £ {totalEarnings.toFixed(2)}</div>
        )}
      </div>
      <div className="incomeExpenseContainer ">
        <table>
          <tbody>
            <tr>
              <td colSpan="2" className="monthNameContainer">
                <h4>{currentMonthName} Month</h4>
              </td>
            </tr>
            <tr>
              <td>
                <h5>Days</h5>
              </td>
              <td>
                <h5> Hours</h5>
              </td>
            </tr>
            <tr>
              <td>
                <p className="days">{totalDaysWorked} d</p>
              </td>
              <td>
                <p className="hours">{totalHoursFormatted}</p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}
